.frequently-asked-questions {
  padding-bottom: 80px;

  h2 {
    padding-bottom: 32px;
  }

  .owl-carousel {
    .owl-stage-outer {
      overflow: visible;
    }

    .owl-item {
      opacity: 0;
      pointer-events: none;
      transition: opacity 0.3s ease;

      &.active {
        opacity: 1;
        pointer-events: auto;
      }
    }
  }

  .owl-theme .owl-nav {
    z-index: 999;
    position: absolute;
    width: 100%;
    height: 0;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;

    [class*=owl-] {
      border-radius: 43px;

      &:hover {
        background: $white-default;
      }
    }

    button {
      position: absolute;
      display: inline-block;
      top: 0;
      bottom: 0;
      width: 44px;
      height: 44px;
      background: $white-default;
      box-shadow: 0 1px 7px fade-out($blackOne, 0.91);
      cursor: pointer;
      margin-top: auto;
      margin-bottom: auto;
      transition: opacity 0.3s ease,
      box-shadow 0.2s ease-in-out;
      @media screen and (min-width: 428px) {
        width: 44px;
        height: 44px;
      }

      &:hover {
        box-shadow: 0 1px 7px fade-out($blackOne, 0.95);
      }

      span {
        position: relative;
        display: block;
        height: 100%;
        vertical-align: middle;
      }

      svg {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        width: 10px;
        height: 6.17px;
        vertical-align: middle;
        @media screen and (min-width: 428px) {
          width: 6.59px;
          height: 11.17px;
        }
      }
    }

    button.owl-prev,
    button.owl-next {
      background: $white-default;
    }

    button.owl-prev {
      left: 0;
      margin-left: -1px;

      svg {
        transform: rotate(-180deg);
      }
    }

    button.owl-next {
      right: 0;
      margin-right: -1px;
    }

    .disabled {
      opacity: 0;
    }
  }

  .item {
    position: relative;
    display: block;
    width: 100%;
    max-width: 282px;
    height: 160px;
    padding-bottom: 19px;
    box-shadow: 0 20px 60px 0 fade-out($black-default, 0.95);
    border-radius: 16px;
    background-color: $white-default;
    @media screen and (max-width: 1045px) {
      max-width: 100%;
    }

    &__wrap {
      z-index: 1;
      position: relative;
      overflow: hidden;
      height: 100%;
      padding-right: 16px;
      padding-left: 16px;
    }

    &__title {
      float: left;
      padding-top: 16px;
      padding-bottom: 8px;
      font-size: 20px;
      font-family: "MuseoSansCyrl-700", sans-serif;
      font-weight: 600;
      line-height: 24px;
      vertical-align: top;
      @media screen and (max-width: 1110px) {
        font-size: 16px;
      }

      br {
        @media screen and (max-width: 500px) {
            display: none;
        }
      }

    }

    &__subtitle {
      width: 100%;
      font-size: 15px;
      font-family: "MuseoSansCyrl-500", sans-serif;
      font-weight: 400;
      line-height: 21px;
      color: #949494;
      @media screen and (max-width: 640px) {
        float: left;
      }
    }

    &__text-group {
      float: left;
      width: calc(100% - 110px);
      padding-bottom: 19px;
      @media screen and (min-width: 1045px) and (max-width: 1110px), screen and (max-width: 940px) {
        width: calc(100% - 40px);
      }
      @media screen and (max-width: 800px) {
        width: 100%;
      }
      @media screen and (max-width: 640px) {
        width: calc(100% - 110px);
      }
      @media screen and (max-width: 600px) {
        width: calc(100% - 40px);
      }
      @media screen and (max-width: 500px) {
        width: calc(100% - 115px);
      }

      & + .item__img {
        margin-top: 3px;
        margin-right: -19px;
      }
    }

    &__img {
      float: right;
      margin-right: -15px;
      vertical-align: middle;
      @media screen and (min-width: 1110px) and (max-width: 1270px) {
        height: 100%;
        margin-top: 19px;
      }
      @media screen and (min-width: 1045px) and (max-width: 1110px),
      screen and (min-width: 640px) and (max-width: 940px), screen and (min-width: 500px) and (max-width: 600px) {
        z-index: -1;
        clear: both;
        position: absolute;
        height: auto;
        top: 0;
        right: -37px;
        bottom: 0;
        margin-top: 0;
        margin-bottom: auto;
      }
      @media screen and (min-width: 640px) and (max-width: 800px) {
        display: none;
      }

      img {
        vertical-align: middle;
        @media screen and (max-width: 1270px) {
          width: calc(10px + 6vw);
        }
        @media screen and (max-width: 1110px) {
          width: 100%;
        }
        @media screen and (max-width: 340px) {
          width: 90px;
        }
      }
    }
  }
}
