.where-does-the-delivery-work {
  padding-top: 22px;
  padding-bottom: 80px;

  &__container {
    position: relative;
    display: block;
    max-width: 690px;
    width: 100%;

    p {
      margin: 0;
    }

    h1 {
      padding-bottom: 16px;
      font-size: 18px;
      font-family: "MuseoSansCyrl-900", sans-serif;
      font-weight: 700;
      line-height: 28.8px;
      @media screen and (min-width: 360px) {
        font-size: 20px;
      }
      @media screen and (min-width: 400px) {
        font-size: 24px;
      }

      button {
        padding-top: 3px;
        padding-left: 10px;
      }
    }

    p {
      padding-bottom: 39px;
      font-family: "MuseoSansCyrl-500", sans-serif;
      font-size: 12px;
      font-weight: 400;
      line-height: 21px;
      text-align: left;
      @media screen and (min-width: 380px) {
        font-size: 15px;
      }
    }
  }

  &__contact-us {
    h6 {
      margin: 0 0 17px;
      font-family: "MuseoSansCyrl-700", sans-serif;
      font-size: 12px;
      font-weight: 600;
      line-height: 15.6px;
      @media screen and (min-width: 380px) {
        font-size: 13px;
      }
    }
  }

  &__btn-group {
    button, a {
      position: relative;
      display: block;
      width: 48px;
      height: 48px;
      padding: 5px;
      border-radius: 12px;
      background-color: #F9F9F9;
      font-size: 15px;
      font-weight: 600;
      font-family: "MuseoSansCyrl-700", sans-serif;
      line-height: 18px;
      text-align: center;
      vertical-align: middle;
      color: $white-default;

      svg {
        vertical-align: middle;
        &.online-chat-white {
          vertical-align: middle;
          margin-top: -4px;
        }
      }
    }

    a {
      svg {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        margin: auto;
      }
    }

    .item {
      display: inline-block;
      width: 48px;
      height: 48px;
      vertical-align: middle;

      &:not(:first-child) {
        margin-left: 4px;
      }

      &_big {
        width: 160px;
        @media screen and (min-width: 360px) {
          width: 200px;
        }
        @media screen and (min-width: 390px) {
          width: 224px;
        }

        button {
          width: 100%;
        }
      }

      &_green {
        button {
          background-color: #36BA5F;

          svg {
            margin-right: 8px;
          }
        }
      }
    }
  }
}
