.support-is-here {
  margin-bottom: 82px;

  .container-fluid {
    z-index: 1;
    overflow: hidden;
    position: relative;
    border-radius: 40px;
    background-color: #93BBEA;
  }

  &__wrap {
    padding: 40px 15px 54px;
    @media screen and (max-width: 390px) {
      padding-top: 20px;
      padding-bottom: 30px;
    }
  }

  h1, h4 {
    text-align: center;
    color: $white-default;
  }

  h1 {
    padding-bottom: 4px;
  }

  h4 {
    padding-bottom: 40px;
    br {
      @media screen and (max-width: 576px) {
          display: none;
      }
    }
  }

  &__btn-group {
    text-align: center;
    @media screen and (max-width: 768px) {
      width: 100%;
      max-width: 400px;
      margin: auto;
    }
    @media screen and (max-width: 460px) {
      max-width: 100%;
    }

    button, a {
      display: block;
      width: 100%;
      height: 48px;
      padding: 15px 5px;
      border-radius: 12px;
      background-color: $white-default;
      font-size: 15px;
      font-weight: 600;
      font-family: "MuseoSansCyrl-700", sans-serif;
      line-height: 18px;
      text-align: center;
      vertical-align: middle;

      svg {
        margin-right: 8px;
        vertical-align: middle;
        &.phone {
          vertical-align: top;
        }
      }
    }
  }

  &__btn {
    display: inline-block;
    width: 100%;
    max-width: 160px;
    @media screen and (max-width: 390px) {
      max-width: 100%;
    }
    @media screen and (min-width: 390.98px) {
      margin-right: 2px;
      margin-left: 2px;
    }
    &:nth-child(n+2) {
      @media screen and (max-width: 390px) {
        padding-top: 8px;
      }
    }
    &:nth-child(n+3) {
      @media screen and (max-width: 790px) {
        padding-top: 8px;
      }
    }

  }

  &__bg {
    z-index: -1;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    max-width: 1200px;
    min-width: 600px;
    height: 100%;
    margin: auto;
    text-align: center;
    @media screen and (max-width: 992px) {
      height: 70%;
      margin-top: auto;
      margin-bottom: 0;
    }
    @media screen and (max-width: 768px) {
      height: 50%;
    }
  }
}
