.reference {
  margin-bottom: 80px;

  h2.title {
    padding-right: 10px;
  }

  &__btn-wrap {
    overflow: hidden;
    position: relative;
    padding-top: 20px;
    padding-bottom: 20px;
    box-shadow: 0 0 30px fade-out($black-default, 0.95);
    border-radius: 16px;
    background-color: $white-default;

    &:not(:nth-child(2)) {
      margin-top: 12px;
    }

    &.selected {
      .reference__btn {
        padding-bottom: 4px;
      }

      .reference__content {
        display: block;
      }
    }

    &::after {
      position: absolute;
      content: '';
      top: 0;
      right: 24.42px;
      bottom: 0;
      left: auto;
      width: 6.59px;
      height: 11.17px;
      margin-top: auto;
      margin-bottom: auto;
      background: url("#{$way-img}sprites/svg/arrow-gray.svg") no-repeat center / 100%;
    }
  }

  &__btn {
    overflow: hidden;
    position: relative;
    padding-right: 50px;
    padding-left: 24px;
    font-size: 20px;
    font-weight: 600;
    font-family: "MuseoSansCyrl-700", sans-serif;
    line-height: 24px;

    &__title {
      font-size: 20px;
      font-family: "MuseoSansCyrl-700", sans-serif;
      font-weight: 600;
      line-height: 24px;
    }
  }

  &__content {
    overflow: hidden;
    position: relative;
    display: none;
    width: 100%;
    margin: 0 auto;
    font-size: 13px;
    font-family: "MuseoSansCyrl-500", sans-serif;
    font-weight: 400;
    line-height: 18.2px;
    color: #949494;
    padding-right: 50px;
    padding-left: 24px;
  }
}
