* {
  -webkit-tap-highlight-color: transparent;
}

*,
::after,
::before {
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

html {
  height: 100%;
  overflow-x: hidden;
}

body {
  overflow-x: hidden;
}

body {
  position: relative;
  display: block;
  min-height: 100%;
  height: auto;
  margin: 0;
  padding: 0;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  background: $white-default;
  -webkit-text-size-adjust: 100%;
  font-family: "Euclid Circular B", sans-serif;
  font-weight: normal;
  font-size: 16px;
  font-style: normal;
  font-feature-settings: "kern" 1;
  font-kerning: normal;
  letter-spacing: 0;
  word-spacing: 0.001em;
  line-height: $line_height150;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $black-default;
}

.main {
  &__container {
    padding: 15px;
    @media screen and (min-width: 390px) {
      padding: 20px;
    }
    @media screen and (min-width: 430px) {
      padding: 40px;
    }
  }
}

button:active,
button:focus,
a:active,
a:focus {
  outline: none;
}

img {
  display: inline-block;
  vertical-align: middle;
  max-width: 100%;
}

img,
a {
  border: none;
}

button {
  padding: 0;
  border: none;
  background: none;
  cursor: pointer;
}

a:active,
a:focus,
a:focus-visible {
  outline: none !important;
}

a {
  text-decoration: none;
  color: $black-default;
  -webkit-tap-highlight-color: transparent; // Safari
}

