h1 {
  margin: 0;
  &.title {
    font-size: 60px;
    font-weight: 700;
    font-family: "MuseoSansCyrl-900", sans-serif;
    line-height: 72px;
    @media screen and (max-width: 800px) {
      font-size: 32px;
    }
    @media screen and (max-width: 576px) {
      font-size: 30px;
    }
    @media screen and (max-width: 480px) {
      font-size: 27px;
    }
  }
}

h2 {
  margin: 0;
  &.title {
    padding-bottom: 32px;
    font-size: 40px;
    font-weight: 600;
    font-family: "MuseoSansCyrl-700", sans-serif;
    line-height: 48px;
    text-align: center;
    @media screen and (max-width: 992px) {
      font-size: 24px;
    }
  }
}
h3 {
  &.title,
  &.subtitle {
    font-size: 20px;
    font-weight: 300;
    font-family: "MuseoSansCyrl-300", sans-serif;
    line-height: $line_height150;

    @media screen and (max-width: 1280px) {
      font-size: calc(10px + 0.7vw);
    }
  }
}

h4 {
  margin: 0;
  &.subtitle {
    font-size: 17px;
    font-weight: 400;
    font-family: "MuseoSansCyrl-500", sans-serif;
    line-height: 23.8px;
    @media screen and (max-width: 576px) {
      font-size: 14px;
    }
  }
}

h5 {
  margin: 0;
  &.subtitle {
    font-size: 15px;
    font-weight: 700;
    font-family: "MuseoSansCyrl-700", sans-serif;
    line-height: 18px;
    @media screen and (max-width: 992px) {
      font-size: 12px;
    }
  }
}
