@font-face {
    font-family: 'Euclid Circular B';
    src: url('#{$font-path}EuclidCircularB/EuclidCircularB-Regular.eot');
    src: local('Euclid Circular B Regular'), local('EuclidCircularB-Regular'),
    url('#{$font-path}EuclidCircularB/EuclidCircularB-Regular.eot?#iefix') format('embedded-opentype'),
    url('#{$font-path}EuclidCircularB/EuclidCircularB-Regular.woff2') format('woff2'),
    url('#{$font-path}EuclidCircularB/EuclidCircularB-Regular.woff') format('woff'),
    url('#{$font-path}EuclidCircularB/EuclidCircularB-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

