.social-media-and-community {
  margin-bottom: 80px;

  .container {
    width: 100%;
    padding: 42px 40px;
    border-radius: 40px;
    box-shadow: 0 20px 60px 0 fade-out($black-default, 0.95);
    background-color: $white-default;
    @media screen and (max-width: 1050px) {
      padding: 32px 30px;
    }
    @media screen and (max-width: 790px) {
      display: inline-block;
    }
    @media screen and (max-width: 576px) {
      padding: 22px 20px;
    }
  }

  .title {
    padding-bottom: 0;
    text-align: left;
    @media screen and (max-width: 576px) {
      padding-bottom: 32px;
      text-align: center;
    }
  }

  .subtitle {
    padding-bottom: 16px;
    @media screen and (max-width: 576px) {
      text-align: center;
    }
  }

  &__block-text, &__social-networks {
    display: inline-block;
    width: 50%;
    vertical-align: middle;
  }

  &__block-text {
    @media screen and (max-width: 1050px) {
      width: 35%;
    }
    @media screen and (max-width: 790px) {
      width: max-content;
    }
    @media screen and (max-width: 576px) {
      width: 100%;
    }
  }

  &__social-networks {
    @media screen and (max-width: 1050px) {
      width: 65%;
    }
    @media screen and (min-width: 576.98px) and (max-width: 790px) {
      width: 212px;
      float: right;
    }
    @media screen and (max-width: 576px) {
      width: 100%;
      text-align: center;
    }
    @media screen and (min-width: 576.98px) {
      margin-right: -5px;
      text-align: right;
    }
    @media screen and (max-width: 540px) {
      display: block;
      width: 212px;
      margin: auto;
    }

    a {
      position: relative;
      display: inline-block;
      width: 56px;
      height: 56px;
      margin-right: 7px;
      margin-left: 7px;
      border-radius: 13.33px;
      text-align: center;
      vertical-align: middle;
      background-color: #F9F9F9;
      &:last-child {
        margin-right: 4px;
      }

      &:nth-child(n+4) {
        @media screen and (max-width: 540px) {
          margin-top: 14px;
        }
        @media screen and (min-width: 576px) and (max-width: 790px) {
          margin-top: 14px;
        }
      }

      svg {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        margin: auto;
      }
    }
  }
}
